.main-header {
  color: var(--color-black);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.18;
  letter-spacing: -0.04em;
  margin-bottom: 40px;
}

.bestsellers-list {
  min-width: 288px;
}

@media only screen and (min-width: 375px) {
  .bestsellers-list {
    min-width: 335px;
  }
}

@media only screen and (min-width: 768px) {
  .bestsellers-list {
    min-width: 704px;
  }
}

@media only screen and (min-width: 1440px) {
  .bestsellers-list {
    min-width: 996px;
  }
}

@media only screen and (min-width: 768px) {
  .main-header {
    font-size: 48px;
    line-height: 1.08;
  }
}

.main-header-accent {
  color: var(--color-blue);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.18;
  letter-spacing: -0.04em;
}

@media only screen and (min-width: 768px) {
  .main-header-accent {
    font-size: 48px;
    line-height: 1.08;
  }
}

.book-card {
  display: none;
  cursor: pointer;
}

@media only screen and (max-width: 767px) {
  .book-card:first-child {
    display: block;
  }
}

@media only screen and (min-width: 768px) {
  .book-card:nth-child(-n + 3) {
    display: block;
  }
}

@media only screen and (min-width: 1440px) {
  .book-card {
    max-width: 180px;
    display: block;
  }
}

.book-card-img {
  border-radius: 8px;
  width: 335px;
  height: 485px;
}

@media only screen and (min-width: 768px) {
  .book-card-img {
    width: 218px;
    height: 316px;
  }
}

@media only screen and (min-width: 1440px) {
  .book-card-img {
    width: 180px;
    height: 256px;
  }
}

.book-card-title {
  margin-top: 14px;
  margin-left: auto;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.12;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  margin-bottom: 4px;
  max-width: 335px;
}

@media only screen and (min-width: 768px) {
  .book-card-title {
    max-width: 218px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

@media only screen and (min-width: 1440px) {
  .book-card-title {
    max-width: 180px;
  }
}

.book-card-text {
  color: var(--color-gray);
  font-size: 12px;
  font-style: italic;
  line-height: 1.16;
  letter-spacing: -0.04em;
  max-width: 335px;
}

@media only screen and (min-width: 768px) {
  .book-card-text {
    max-width: 218px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

@media only screen and (min-width: 1440px) {
  .book-card-text {
    max-width: 180px;
  }
}

.book-card-btn {
  color: var(--color-black);
  font-weight: 700;
  line-height: 1.28;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  display: block;
  margin-bottom: 40px;
  margin-left: auto;
  padding: 14px 28px;
  min-width: 124px;
  min-height: 46px;
  background-color: transparent;
  border: 2px solid var(--color-blue);
  border-radius: 40px;
  transition:
    color 500ms ease-in-out,
    background-color 500ms ease-in-out;
}

@media only screen and (min-width: 768px) {
  .book-card-btn {
    margin-bottom: 40px;
  }
}

.book-card-btn:hover {
  color: var(--color-white);
  background-color: var(--color-blue);
}

.top-category-list {
  display: flex;
  gap: 24px;
  margin-bottom: 8px;
}

@media only screen and (min-width: 768px) {
  .top-category-list {
    margin-bottom: 20px;
  }
}

@media only screen and (min-width: 1440px) {
  .top-category-list {
    margin-bottom: 32px;
  }
}

.top-category-title {
  color: var(--color-gray);
  line-height: 1.28;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin-bottom: 18px;
}

@media only screen and (min-width: 768px) {
  .top-category-title {
    line-height: 1.71;
  }
}

/* ===================== Book card backdrop ========================= */

.book-card-wrapper {
  position: relative;
  overflow: hidden;
}

.book-text-backdrop {
  background-color: var(--color-blue-gold);
  font-size: 30px;
  text-transform: uppercase;
  text-align: center;
  border-radius: 0 0 8px 8px;
  color: var(--color-white);
  line-height: 1.5;
  letter-spacing: -0.02em;
  padding: 27px 32px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 20%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s cubic-bezier(0.4, 0, 0.2, 1);
}

@media only screen and (min-width: 768px) {
  .book-text-backdrop {
    font-size: 20px;
    padding: 17px 32px;
  }
}

@media only screen and (min-width: 1440px) {
  .book-text-backdrop {
    font-size: 16px;
    padding: 15px 28px;
  }
}

.book-card:hover .book-text-backdrop {
  visibility: visible;
  opacity: 1;
  transform: translateY(400%);
}

.book-card:focus .book-text-backdrop {
  visibility: visible;
  opacity: 1;
  transform: translateY(400%);
}
